'use client';

import { useState } from 'react';
import {
  Container,
  Box,
  TextField,
  Button,
  Typography,
  Paper,
  InputAdornment,
  IconButton,
  Alert,
  Fade,
  Slide,
} from '@mui/material';
import { Visibility, VisibilityOff, Email, Lock } from '@mui/icons-material';
import Link from 'next/link';
import { validateEmail, validatePassword } from '@/lib/validation';
import { motion } from 'framer-motion';
import FloatingShapes from '@/app/components/FloatingShapes';
import ThemeToggle from '@/app/components/ThemeToggle';
import { useTheme } from '@mui/material/styles';

/**
 * Login Page Component
 * Features:
 * - Email and password validation
 * - Beautiful animations and hover effects
 * - Material UI components with custom styling
 * - Responsive design
 */
export default function LoginPage() {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';
  
  // State management for form fields
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  
  // State management for validation errors
  const [emailError, setEmailError] = useState('');
  const [passwordError, setPasswordError] = useState('');
  
  // State management for form submission
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState('');
  const [submitSuccess, setSubmitSuccess] = useState(false);

  /**
   * Handles email input change and validates in real-time
   */
  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setEmail(value);
    
    // Clear error when user starts typing
    if (emailError) {
      setEmailError('');
    }
    
    // Validate on blur or when field is not empty
    if (value) {
      const error = validateEmail(value);
      setEmailError(error);
    }
  };

  /**
   * Handles password input change and validates in real-time
   */
  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setPassword(value);
    
    // Clear error when user starts typing
    if (passwordError) {
      setPasswordError('');
    }
    
    // Validate on blur or when field is not empty
    if (value) {
      const error = validatePassword(value);
      setPasswordError(error);
    }
  };

  /**
   * Handles form submission
   * Validates all fields and processes login
   */
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    
    // Reset previous states
    setSubmitError('');
    setSubmitSuccess(false);
    
    // Validate all fields
    const emailValidation = validateEmail(email);
    const passwordValidation = validatePassword(password);
    
    setEmailError(emailValidation);
    setPasswordError(passwordValidation);
    
    // If there are validation errors, don't submit
    if (emailValidation || passwordValidation) {
      return;
    }
    
    // Set submitting state
    setIsSubmitting(true);
    
    // Simulate API call (replace with actual API call)
    try {
      await new Promise((resolve) => setTimeout(resolve, 1500));
      
      // In a real application, you would make an API call here
      // const response = await fetch('/api/login', { ... });
      
      setSubmitSuccess(true);
      
      // Reset form after successful submission
      setTimeout(() => {
        setEmail('');
        setPassword('');
        setSubmitSuccess(false);
      }, 2000);
    } catch {
      setSubmitError('Login failed. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Box
      className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8 relative"
      sx={{
        background: isDark
          ? 'linear-gradient(135deg, #0f172a 0%, #1e1b4b 50%, #312e81 100%)'
          : 'linear-gradient(135deg, #eef2ff 0%, #faf5ff 50%, #fdf2f8 100%)',
        position: 'relative',
        overflow: 'hidden',
        transition: 'background 0.3s ease',
      }}
    >
      <Box
        sx={{
          position: 'absolute',
          top: 16,
          right: 16,
          zIndex: 10,
        }}
      >
        <ThemeToggle />
      </Box>
      <FloatingShapes />
      <Container maxWidth="sm" sx={{ position: 'relative', zIndex: 1 }}>
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, ease: 'easeOut' }}
        >
          <Paper
            elevation={0}
            component={motion.div}
            whileHover={{ 
              scale: 1.02,
              boxShadow: isDark 
                ? '0 25px 70px rgba(99, 102, 241, 0.3)'
                : '0 25px 70px rgba(99, 102, 241, 0.2)',
            }}
            className="p-8 sm:p-10 rounded-3xl shadow-2xl backdrop-blur-sm"
            sx={{
              background: isDark
                ? 'rgba(30, 41, 59, 0.8)'
                : 'rgba(255, 255, 255, 0.9)',
              backdropFilter: 'blur(10px)',
              borderRadius: '24px',
              boxShadow: isDark
                ? '0 20px 60px rgba(0, 0, 0, 0.5)'
                : '0 20px 60px rgba(0, 0, 0, 0.1)',
              border: `1px solid ${isDark ? 'rgba(99, 102, 241, 0.2)' : 'rgba(255, 255, 255, 0.2)'}`,
              transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
            }}
          >
            {/* Header Section */}
            <Fade in={true} timeout={800}>
              <Box className="text-center mb-8">
                <Typography
                  variant="h4"
                  className="mb-2 bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent font-bold"
                  sx={{
                    background: 'linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%)',
                    WebkitBackgroundClip: 'text',
                    WebkitTextFillColor: 'transparent',
                    fontWeight: 700,
                    letterSpacing: '-0.02em',
                  }}
                >
                  Welcome Back
                </Typography>
                <Typography
                  variant="body2"
                  className="text-gray-600"
                  sx={{ color: '#6b7280' }}
                >
                  Sign in to your account to continue
                </Typography>
              </Box>
            </Fade>

            {/* Success/Error Messages */}
            {submitSuccess && (
              <Fade in={submitSuccess}>
                <Alert
                  severity="success"
                  className="mb-6 rounded-xl"
                  sx={{
                    borderRadius: '12px',
                    animation: 'slideIn 0.3s ease-out',
                  }}
                >
                  Login successful! Redirecting...
                </Alert>
              </Fade>
            )}

            {submitError && (
              <Fade in={!!submitError}>
                <Alert
                  severity="error"
                  className="mb-6 rounded-xl"
                  sx={{
                    borderRadius: '12px',
                    animation: 'slideIn 0.3s ease-out',
                  }}
                >
                  {submitError}
                </Alert>
              </Fade>
            )}

            {/* Login Form */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.2 }}
            >
              <Box component="form" onSubmit={handleSubmit} className="space-y-6">
                {/* Email Field */}
                <motion.div
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.5, delay: 0.3 }}
                  whileHover={{ scale: 1.02 }}
                >
                  <TextField
                  fullWidth
                  label="Email Address"
                  type="email"
                  value={email}
                  onChange={handleEmailChange}
                  onBlur={() => {
                    if (email) {
                      setEmailError(validateEmail(email));
                    }
                  }}
                  error={!!emailError}
                  helperText={emailError}
                  placeholder="you@example.com"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Email className="text-indigo-500" />
                      </InputAdornment>
                    ),
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      '&:hover fieldset': {
                        borderColor: '#6366f1',
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#6366f1',
                        borderWidth: '2px',
                      },
                    },
                    '& .MuiInputLabel-root.Mui-focused': {
                      color: '#6366f1',
                    },
                  }}
                  className="transition-all duration-300"
                  />
                </motion.div>

                {/* Password Field */}
                <motion.div
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.5, delay: 0.4 }}
                  whileHover={{ scale: 1.02 }}
                >
                  <TextField
                  fullWidth
                  label="Password"
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={handlePasswordChange}
                  onBlur={() => {
                    if (password) {
                      setPasswordError(validatePassword(password));
                    }
                  }}
                  error={!!passwordError}
                  helperText={passwordError}
                  placeholder="Enter your password"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Lock className="text-indigo-500" />
                      </InputAdornment>
                    ),
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          onClick={() => setShowPassword(!showPassword)}
                          edge="end"
                          className="text-gray-500 hover:text-indigo-600 transition-colors"
                          sx={{
                            '&:hover': {
                              backgroundColor: 'rgba(99, 102, 241, 0.1)',
                            },
                          }}
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      '&:hover fieldset': {
                        borderColor: '#6366f1',
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#6366f1',
                        borderWidth: '2px',
                      },
                    },
                    '& .MuiInputLabel-root.Mui-focused': {
                      color: '#6366f1',
                    },
                  }}
                  className="transition-all duration-300"
                  />
                </motion.div>

                {/* Forgot Password Link */}
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ duration: 0.5, delay: 0.5 }}
                >
                  <Box className="flex justify-end">
                    <Link
                      href="/forgot-password"
                      className="text-sm text-indigo-600 hover:text-indigo-800 font-medium transition-colors duration-200 hover:underline"
                    >
                      Forgot password?
                    </Link>
                  </Box>
                </motion.div>

                {/* Submit Button */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.6 }}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    disabled={isSubmitting}
                    className="py-3 mt-6 bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-[1.02] active:scale-[0.98]"
                    sx={{
                      background: 'linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%)',
                      padding: '14px 24px',
                      fontSize: '1rem',
                      fontWeight: 600,
                      borderRadius: '12px',
                      textTransform: 'none',
                      boxShadow: '0 4px 14px rgba(99, 102, 241, 0.4)',
                      '&:hover': {
                        background: 'linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%)',
                        boxShadow: '0 6px 20px rgba(99, 102, 241, 0.5)',
                        transform: 'translateY(-2px)',
                      },
                      '&:active': {
                        transform: 'translateY(0)',
                      },
                      '&.Mui-disabled': {
                        background: '#9ca3af',
                      },
                    }}
                  >
                    {isSubmitting ? 'Signing in...' : 'Sign In'}
                  </Button>
                </motion.div>

                {/* Sign Up Link */}
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ duration: 0.5, delay: 0.7 }}
                >
                  <Box className="text-center mt-6">
                    <Typography variant="body2" className="text-gray-600">
                      Don&apos;t have an account?{' '}
                      <Link
                        href="/signup"
                        className="text-indigo-600 hover:text-indigo-800 font-semibold transition-colors duration-200 hover:underline"
                      >
                        Sign up
                      </Link>
                    </Typography>
                  </Box>
                </motion.div>
              </Box>
            </motion.div>
          </Paper>
        </motion.div>
      </Container>
    </Box>
  );
}

